article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section, summary {
    display: block;
}


.WikiaRail .module h3, a, abbr, address, article, aside, audio, b, blockquote, body, canvas, caption, cite, code, dd, del, details, dfn, div, dl, dt, em, embed, fieldset, figcaption, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, html, i, iframe, img, ins, kbd, label, legend, li, mark, menu, nav, object, ol, p, pre, q, samp, section, small, span, strong, sub, summary, sup, time, ul, var, video {
    border: 0;
    margin: 0;
    padding: 0;
    vertical-align: baseline;
}


li{
    display: list-item;
    text-align: 0 0 8px rgb(234, 86, 177);
}
a{
    text-decoration-line: none;
    color: white;
}



.Wiki-header{
    background: url(../Components/images/header-background.jpg) #db1846 no-repeat right center;
    background-size: contain;
    content: "";
    display: block;
    height: 100%;
    height: 105px;
    position: relative;
    width: 100%;
    z-index: 1;
}

.Wiki-header:before{
    --theme-header-gradient-start: 200px;
    --theme-header-gradient-end: 471px;
    --theme-header-background-color: #db1846;
    background: linear-gradient(to right,  rgba(219,24,70,0) 50%,rgba(219,24,70,1) 55%,rgba(219,24,70,1) 60%,rgba(219,24,70,0.3) 71%,rgba(219,24,70,0.3) 87%,rgba(219,24,70,0.79) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */

    content: "";
    display: block;
    height: 100%;
    position: absolute;
    width: 100%;
    //z-index: 21;
}

.Wiki-header-mark{
    float: left;
    height: 65px;
    margin: 20px 0px 20px 20px;
    max-width: 250px;
    width: auto;
}

.Wiki-header-top{
    height: 59px;
    display: flex;
    flex-wrap: wrap;
}

.Wiki-header-sitename{
    color: white;
    word-wrap: break-word;
    font-size: 24px;
    margin-top: 20px;
    max-height: 72px;
    white-space: normal;
    flex: 1;
    font-weight: 600;
    line-height: 1;
    margin: 25px 0 0 18px;
    overflow: hidden;
}

#Wiki-header-bottom{
    height: 48px;
    width: 480px;
    display: inline-block;
    position: relative;
    overflow: hidden;
}

.Wiki-header-bottom ul{
    list-style-type: none;
    margin:0px;
    padding:0px;
    //z-index: -1;
}

.Wiki-header-bottom ul li{
    color: white;
    background: #db1846;
    float: left;
    line-height: 46px;
    vertical-align: middle;
    text-align: center;
    position:relative;
}

.menulink, .submenulink{
    text-decoration: none;
    display: block;
    width:120px;
    font-size: 14px;
    font-weight: 600;

}

.topmenu a{
    cursor: pointer;
}
.submenu{
    display: none;
    position: absolute;
}

.submenu li a{
    color: white;
    background-color: #db1846;
    text-decoration: none;
    display: block;
    cursor: pointer;
}


.topmenu:hover ul{
    display: block;
}