.page{
    color: wheat;
    margin-top: 12px;
    opacity: 0.79;
    position: relative;
}

.page_main{
    background-color: #0d0f21;
    border-radius: 3px;
    min-height: 480px;
    opacity: 0.79;
    padding: 24px 36px;
    position: relative;
}

*, ::after, ::before{
    box-sizing: inherit;
}

main{
    display: block;
}

body{
    font-size: 14px;
    line-height: 1.75px;
}